export class Icon {
  public element: HTMLElement;
  constructor(
    parentElement: HTMLElement | null,
    options: {
      name: string;
      size?: number;
      color?: string;
      className?: string;
      style?: Partial<CSSStyleDeclaration>;
    },
  ) {
    const targetIcon = svgIconList[options.name];
    if (targetIcon) {
      this.element = createElementFromHtmlString(svgIconList[options.name]);
      const size = options.size ? options.size : 16;
      const color = options.color ? options.color : '#333333';
      const styleObj: Partial<CSSStyleDeclaration> = {
        width: size + 'px',
        height: size + 'px',
      };
      if (options.color) {
        styleObj.fill = color;
        styleObj.color = color;
      }
      setStyles(this.element, styleObj);
      if (options.style) {
        setStyles(this.element, options.style);
      }
      if (options.className) this.element.setAttribute('class', options.className);
      this.element.classList.add('tfgraph-icon');
      parentElement.appendChild(this.element);
    }
  }
}
